import React, { Component } from 'react'
import { Table, Container, Form } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEdit, faTrash } from '@fortawesome/free-solid-svg-icons'
import NavBarMenu from './NavBarMenu'


export default class RestaurantSearch extends Component {
    constructor() {
        super()
        this.state = {
            searchData: null,
            noData: false,
            lastSearch:""
        }
    }
    search(key) {
        this.setState({lastSearch:key})
        fetch('http://localhost:3000/restaurant?q=' + key).then((data) => {
            data.json().then((response) => {
                if (response.length > 0) {
                    this.setState({ searchData: response, noData: false })

                }
                else (
                    this.setState({ noData: true, searchData: null })
                )
            })
        })

    }

    delete(id) {
        fetch('http://localhost:3000/restaurant/' + id, {
            method: "DELETE"
        }).then((response) => {
            response.json().then((result) => {
                this.search(this.state.lastSearch)

            })
        })
    }
    render() {
        return (
            <Container>
                <NavBarMenu />
                <h1>Restaurant Search</h1>
                <Form.Control type="text" onChange={(e) => this.search(e.target.value)} placeholder="Search Restaurant" />
                <div>
                    {
                        this.state.searchData ?
                            <div>
                                <Table striped bordered hover>
                                    <thead>
                                        <tr>
                                            <th>Id</th>
                                            <th>Name</th>
                                            <th>Address</th>
                                            <th>Rating</th>
                                            <th>Operation</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                    {
                                        this.state.searchData.map((item) =>
                                        <tr>
                                        <td>{item.id}</td>
                                        <td>{item.name}</td>
                                        <td>{item.address}</td>
                                        <td>{item.rating}</td>
                                        <td><Link to={"/update/" + item.id} ><FontAwesomeIcon icon={faEdit} color='orange' /></Link>
                                            <span onClick={() => this.delete(item.id)}><FontAwesomeIcon icon={faTrash} color='red' /></span></td>
                                        </tr>
                                        )
                                    }
                                    </tbody>
                                    </Table>
                            </div>
                                :" "
                            }
                                {
                                    this.state.noData ? <h3>No Data Found</h3> : null
                                }
                            </div>
            </Container>
                )
            }
        }
    
