import React, { Component } from 'react'
import NavBarMenu from './NavBarMenu'
import { Container } from 'react-bootstrap'
 

export default class RestaurantUpdate extends Component {
    constructor(){
        super()
        this.state = {
            name:null,
            address:null,
            rating:null,
            email:null,
            id:null
        }
    }

    componentDidMount (){
        fetch('http://localhost:3000/restaurant/'+ this.props.match.params.id).then((response) => {
            response.json().then((result) => {
                this.setState({ 
                    name:result.name,
                    address:result.address,
                    rating:result.rating,
                    email:result.email,
                    id:result.id

                 })
            })
        })
    }
update (){
    fetch('http://localhost:3000/restaurant/'+ this.state.id, {
        method: "PUT",
        headers: {
            'Content-Type': 'application/json'
        },
        body:JSON.stringify(this.state)
    }).then((response)=>{
        response.json().then((result) =>{
        })
    })
}
    render() {
        return (
            <Container>
                <NavBarMenu />
                <h1>Restaurant Update</h1>
                <input onChange={(e)=>{this.setState({name:e.target.value})}} 
                placeholder = "Name" value={this.state.name} /> <br/><br/>
                <input onChange={(e)=>{this.setState({address:e.target.value})}} 
                placeholder = "Address" value={this.state.address}/> <br/><br/>
                <input onChange={(e)=>{this.setState({rating:e.target.value})}} 
                placeholder = "Rating" value={this.state.rating}/> <br/><br/>
                <input onChange={(e)=>{this.setState({email:e.target.value})}} 
                placeholder = "Email" value={this.state.email}/> <br/><br/>
                <button onClick={() =>{this.update()}}>Update Restaurant</button>
            </Container>
        )
    }
}
